  .skills_grid-item {
    position: relative;
    width:350px;
    height:300px;
    background-color: rgba(0, 0, 0, 0.2); /* Black with faded opacity */
    border-radius: 20px; /* Rounded border */
    border: 2px solid rgba(0, 0, 0, 0); /* Transparent border */
    box-shadow: 0 0 10px rgba(215, 87, 28, 0.7); /* Orange shadow effect */
    display: flex;
    flex-direction: column; /* Display the logo and text in a column layout */
    justify-content: center;
    align-items: center;
    padding: 20px; /* Adding padding inside the rectangles */
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    transition: -webkit-transform 0.4s ease;
    transition: transform 0.4s ease;
    transition: transform 0.4s ease, -webkit-transform 0.4s ease; /* Animation transition */
    -webkit-perspective: 1000px;
            perspective: 1000px;
    cursor: pointer; /* Add cursor: pointer to indicate that it's clickable */
    /* Set the back class to initially have a rotated state */
  }
  /* Class to trigger the toggling animation */
  .skills_grid-wrapper.scrolling .skills_grid-item {
    -webkit-transform: translateY(20px);
            transform: translateY(20px); /* Distance to toggle up or down */
  }

  
  .skills_grid-item p {
    color: rgb(255,255 ,255); /* Text color */
    font-size: 14px; /* Text font size */
