    margin-top: 10px; /* Add some space between the logo and text */
  }
  /* Add styles for the "Click to read more" text */
  .click-to-read {
    position: absolute;
    bottom: -23px;
    right: 10px;
    color: #fff;
    background-color: orangered;
    padding: 5px 10px;
    border-radius: 4px;
    font-size: 12px;
    opacity: 0;
    pointer-events: none;
